import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import styled from "styled-components"

const Container = styled.header`
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  background-color: rebeccapurple;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  padding: 0 5%;
`
const IconsContainer = styled.section`
  display: flex;
  flex-flow: row nowrap;
`
const IconContainer = styled.div`
  margin: auto 5px;
`

const Header = ({ siteTitle }) => (
  <Container>
    <Link
      to="/"
      style={{
        color: `white`,
        textDecoration: `none`,
      }}
    >
      <div>{siteTitle}</div>
    </Link>
    <IconsContainer>
      <IconContainer>
        <a
          href="http://twitter.com/leogouveia"
          alt="Twitter Link"
          target="_blank"
          rel="noopener noreferrer"
        >
          <i className="nes-icon twitter"></i>
        </a>
      </IconContainer>
      <IconContainer>
        <a
          href="http://github.com/leogouveia"
          alt="Github Link"
          target="_blank"
          rel="noopener noreferrer"
        >
          <i className="nes-icon github"></i>
        </a>
      </IconContainer>
      <IconContainer>
        <a
          href="https://www.linkedin.com/in/leonardo-gouveia/"
          alt="Linkedin Link"
          target="_blank"
          rel="noopener noreferrer"
        >
          <i className="nes-icon linkedin"></i>
        </a>
      </IconContainer>
    </IconsContainer>
  </Container>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
